<template id='Header'>
  <div class="head">
    <header @click="threeClick()">
      <img src="../assets/logo.jpg" alt />
    </header>
    <main>
      <span>{{title}}</span>
    </main>
  </div>
</template>

<style scoped>
.head {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  font-size: 0.32rem;
  z-index: 10;
  padding-bottom: 0.4rem;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
header {
  padding: 0.6rem 0 0.1rem 0;
  height: 100%;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 0.04rem solid black;
}
header img {
  width: 1.5rem;
  height: 1.275rem;
}
main {
  width: 5rem;
  margin-top: 0.04rem;
  border-top: 0.04rem solid black;
  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
main span {
  font-size: 0.34rem;
  font-weight: bold;
}
</style>

<script>
export default {
  name: "Heads",
  props: ["title"],
  data() {
    return {
      threenum: 0
    };
  },
  methods: {
    threeClick() {
      this.threenum++;
      if (this.threenum === 3) {
        this.$router.push({
          name: "Login"
        });
      }
    }
  },
  mounted() {}
};
</script>