import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/code',
    name: 'Code',
    component: () => import('../views/Code.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/configure',
    name: 'Configure',
    component: () => import('../views/Configure.vue')
  },
  {
    path: '/cop',
    name: 'Cop',
    component: () => import('../views/Cop.vue')
  },
  {
    path: '/copcode',
    name: 'Copcode',
    component: () => import('../views/Copcode.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
