<template>
  <div class="home">
    <Heads :title="languages.a1[langIndex]"></Heads>
    <main>
      <section class="head-input">
        <div class="floor-input">
          <van-field
            @keyup.enter="onSubmit()"
            class="floorinput"
            input-align="center"
            v-model="floorvalue"
            :placeholder="languages.a2[langIndex]"
          />
        </div>
      </section>
      <section id="tofloor" class="floor-body">
        <ul v-if="edit==false" class="floor-block">
          <li
            :style="startindex-1==index?'float:none':''"
            v-for="(item, index) in floornums"
            :key="index"
            @click="selectFloor(item)"
            class="blockli"
          >{{item}}</li>
        </ul>
        <ul v-else-if="edit==true" class="floor-block">
          <li style="border-color:#7e7e7e" v-for="(item, index) in editlists" :key="index">
            <van-field
              input-align="center"
              class="editinput"
              v-model="editlists[index]"
              @focus="selectNum(item)"
              @blur="editListFun(editlists[index],index)"
              @keyup.enter="editListFun(editlists[index],index)"
            />
          </li>
        </ul>
      </section>
      <section v-if="footbool" class="edit">
        <div v-if="edit==false" class="tips" @click="edit=true">
          <span>{{languages.a7[langIndex]}}</span>
        </div>
        <div class="efit_class" v-else>
          <div @click="giveUp()">{{languages.a6[langIndex]}}</div>
          <div @click="editListSubmit()">{{languages.a8[langIndex]}}</div>
        </div>
      </section>
    </main>
    <Foots v-if="footbool" :a3="languages.a3[langIndex]" :a4="languages.a4[langIndex]"></Foots>
  </div>
</template>
