<script>
import Heads from "@/components/Head";
import Foots from "@/components/Foot";
import formatTime from "@/model/time.js";
import {Encrypt} from '@/model/utils.js';
export default {
  name: "Home",
  components: { Heads, Foots },
  data() {
    return {
      floorvalue: "", // 输入楼层
      edit: false,
      editvule: "",
      floornums: [],
      editlists: [],
      startindexs: 0,
      footbool: true
    };
  },
  methods: {
    // 选择楼层
    selectFloor(e) {
      let obj = {"registFloor": e,"endTime": formatTime(new Date())};
      let value = Encrypt(JSON.stringify(obj));
      this.$router.push({ name: "Code", params: {encryption: value,floornum: e } });
    },
    // 提交
    onSubmit() {
      let obj = {"registFloor": this.floorvalue,"endTime": formatTime(new Date())};
      let value = Encrypt(JSON.stringify(obj));
      this.$router.push({
        name: "Code",
        params: { encryption: value, floornum: this.floorvalue }
      });
    },
    // 选中要修改的楼层
    selectNum(item){
      this.editvule = item;
      this.footbool = false;
    },
    // 编辑常用楼层
    editListFun(e, i) {
      this.footbool = true;
      let index = this.floornums.indexOf(this.editvule);
      let branch = index < this.floornums.indexOf(e) ? false : true;
      let exist = this.floornums.indexOf(e)!=-1;  // 是否存在这个楼层
      this.floornums = this.floornums.filter(value => {
        return value != e;
      });
      if (this.editvule == e) {
        return;
      }
      if (i == 0 && this.editvule == -1) {
        this.floornums.splice(index + 1, 0, e);
      } else if (branch&&exist) {
        this.floornums.splice(index - 1, 0, e);
      } else {
        this.floornums.splice(index, 0, e);
      }
      let e_index = this.floornums.indexOf(e); //获取小方块的起始下标
      e_index = e_index - i;
      this.startindexs = e_index;
      let e_arr = this.floornums.slice(e_index, e_index + 9); // 截取获得小方块的数组
      this.editlists = e_arr;
    },
    // 提交常用
    editListSubmit() {
      this.$store.commit("getFloorNum", this.floornums);
      this.$store.commit("getEditList", this.editlists);
      this.$store.commit("getStartIndex", this.startindexs);
      this.edit = false;
      this.toScroll();
    },
    // 放弃提交
    giveUp() {
      this.edit = false;
      this.toScroll();
    },
    toScroll() {
      this.floornums = [...this.floornum];
      this.editlists = [...this.editlist];
      this.$nextTick(() => {
        let blockli = document.getElementsByClassName("blockli")[0];
        if (this.startindex > 3) {
          let top = blockli.offsetTop - blockli.offsetHeight / 4;
          document.documentElement.scrollTop = top;
        }else if(this.startindex <= 3&&this.startindex>0){
          let top = blockli.offsetTop - blockli.offsetHeight - blockli.offsetHeight / 4;
          document.documentElement.scrollTop = top;
        }
      });
    }
  },
  watch: {},
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    },
    editlist() {
      return this.$store.state.editlist;
    },
    floornum() {
      return this.$store.state.floornum;
    },
    // 常用按键的起始下标
    startindex() {
      return this.$store.state.startindex;
    }
  },
  mounted() {
    setTimeout(() => {
      this.toScroll();
    }, 60);
  }
};
</script>
